<template>
  <div class="wrapper">
    <div class="box">
      <!-- 中间登录 -->
      <div class="deng">
        <!-- 圆图标 -->
        <div class="ico"><img src="@/assets/logo.png" alt="" /></div>
        <!-- input框 输入用户密码 -->
        <div class="input">
          <div class="inp">
            <el-form
              :model="ruleForm"
              :rules="rules"
              ref="ruleForm"
              label-width="100px"
              class="demo-ruleForm"
            >
              <el-form-item label="" prop="username">
                <el-input
                  prefix-icon="el-icon-s-custom"
                  placeholder="请输入用户名"
                  v-model="ruleForm.username"
                ></el-input>
              </el-form-item>

              <el-form-item label="" prop="password">
                <el-input
                  prefix-icon="el-icon-lock"
                  placeholder="请输入密码"
                  type="password"
                  v-model="ruleForm.password"
                ></el-input>
              </el-form-item>
              <!-- button按钮 -->
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"
                  >登录</el-button
                >
                <el-button @click="resetForm('ruleForm')">重置</el-button>
              </el-form-item>

              <!-- <input type="text" placeholder="请输入用户名" /> -->
            </el-form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      ruleForm: {
        username: "admin",
        password: "123456",
      },
      rules: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 15,
            message: "长度在 3 到 15 的用户名",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 3,
            max: 15,
            message: "长度在 3 到 15 的密码",
            trigger: "blur",
          },
        ],
      },
    };
  },
  computed: {},
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert("submit!");
          this.$http
            .post(
              "https://www.liulongbin.top:8888/api/private/v1/login",
              this.ruleForm
            )
            .then((res) => {
              console.log(res.data);

              if (res.data.meta.status == 200) {
                sessionStorage.setItem("token", res.data.data.token);
                this.$router.push("/index");
                this.$message({
                  message: res.data.meta.msg,
                  type: "success",
                });
              } else {
                this.$message({
                  message: res.data.meta.msg,
                  type: "error",
                });
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style scoped lang="scss">
.wrapper {
  width: 100%;
  height: 100%;
}
.box {
  width: 100%;
  height: 660px;
  background: rgb(49, 65, 95);
  position: relative;
}
.deng {
  width: 560px;
  height: 330px;
  background: white;
  position: absolute;
  top: 25%;
  left: 25%;
  position: relative;
  box-shadow: 0 0 10px whitesmoke;
}
.ico {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  background: white;
  justify-content: space-around;
  box-shadow: 0 0 10px rgb(233, 233, 233);
  position: absolute;
  top: -40px;
  left: 38%;
  img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background: whitesmoke;
  }
}
.input {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 130px;
}
.inp {
  margin-right: 100px;
  .el-input {
    width: 410px;
    height: 48px;
  }
}
.button {
  margin-top: 90px;
  margin-left: 296px;
}
.el-form-item {
  display: flex;
  justify-content: flex-end;
}
</style>
