<template>
  <div class="wrapper">
    <el-container>
      <!-- 头部 -->
      <el-header
        >电商后台管理系统<el-button type="info" @click="goOut"
          >退出</el-button
        ></el-header
      >

      <el-container>
        <!-- 左边 -->
        <el-aside
          width="200px"
          :style="{ width: isCollapse ? '64px' : '200px' }"
        >
          <el-radio-group>
            <div @click="isCollapse = !isCollapse">|||</div>
          </el-radio-group>
          <el-menu
            :collapse-transition="false"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            :collapse="isCollapse"
            unique-opened
            text-color="#fff"
            active-text-color="#409EFF"
            router
            :default-active="activePath"
          >
            <!-- 一级菜单 -->
            <el-submenu
              :index="'/' + item.path"
              v-for="(item, index) in menu"
              :key="index"
            >
              <template slot="title">
                <i :class="iconObj[item.id]"></i>
                <span slot="title">{{ item.authName }}</span>
              </template>
              <!-- 二级菜单 -->
              <el-menu-item
                @click="savePath(`/${it.path}`)"
                :index="`/${it.path}`"
                v-for="(it, index) in item.children"
                :key="index"
              >
                <i class="el-icon-present"></i>
                {{ it.authName }}
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <!-- 中间 -->
        <el-main>
          <!-- aaaa -->
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      isCollapse: true,
      menu: [],
      iconObj: {
        125: "el-icon-s-custom",
        103: "el-icon-discover",
        101: "el-icon-shopping-bag-1",
        102: "el-icon-finished",
        145: "el-icon-edit-outline",
      },
      activePath: "",
    };
  },
  watch:{
    $router(to,from){
      console.log(to,from)
      if(to.path=='/welcome'){
        console.log(123)
        this.activePath=null
      }else{
        this.activePath = to.path
      }
      window.sessionStorage.setItem("activePath",to.path)
      // this.activePath = to.path
    }
  },
  computed: {},
  created() {
    let path = window.sessionStorage.getItem("activePath");
    this.activePath = path;
  },
  mounted() {
    this.getMenu();
  },

  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    // 退出登录
    goOut() {
      this.$confirm("此操作将退出该页面, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          sessionStorage.removeItem("token");
          this.$router.push("/");
          this.$message({
            type: "success",
            message: "退出成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消退出",
          });
        });
    },
    // 存储path
    savePath(path) {
      window.sessionStorage.setItem("activePath", path);
      this.activePath = path;
    },

    // 左菜单栏
    getMenu() {
      // let data = sessionStorage.getItem("token")
      // console.log(data)
      this.$http
        .get("https://www.liulongbin.top:8888/api/private/v1/menus")
        .then((res) => {
          console.log(res.data.data);
          this.menu = res.data.data;
        });
    },
  },
};
</script>

<style scoped>
* {
  padding: 0;
  margin: 0;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.el-header,
.el-footer {
  background-color: #46484b;
  color: white;
  font-size: 22px;
  line-height: 60px;
}
.el-button--info {
  width: 60px;
  height: 40px;
  margin-left: 78%;
}
.el-aside {
  background-color: #424e5c;
  color: #333;
  text-align: center;
}
li.el-menu-item {
  background: #354250;
  color: white;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  /* text-align: center; */
  /* line-height: 160px; */
}
.el-radio-group {
  color: white;
  width: 100%;
  font-size: 16px;
  height: 24px;
  text-align: center;
  line-height: 24px;
  background-color: #3e5979;
}

body > .el-container {
  margin-bottom: 40px;
}
section.el-container {
  height: 689px;
}
.el-menu--collapse {
  /* width: 199px; */
  background-color: #424e5c;
}
.el-menu {
  background-color: #424e5c;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}
el-submenu {
  background-color: #445569;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>
